import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { withStyles } from '@material-ui/core/styles';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import Grid from '@material-ui/core/Grid';
import FormControl from '@material-ui/core/FormControl';
import Button from '@material-ui/core/Button';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Typography from '@material-ui/core/Typography';
import { login } from '../../services/users/actions';
import {Recorder} from 'react-voice-recorder'
import 'react-voice-recorder/dist/index.css'
import { compose } from 'redux';
import axios from '../../services/axios';
const { createRef } = React;

const styles = theme => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  margin: {
    margin: theme.spacing.unit,
  },
  fill: {
    flexBasis: '100%',
  },
});
class Index extends Component {
  state = {
    id: '',
    name: '',
    home: '',
    country: '',
    gender: '',
    age: '',
    voice: '',
    pitch: '',
    pace: '',
    randomTextIndex: 0,
    hide: false,
    settingList: [],
    fruits : ["", "", "", "", "", ""],
    fruit1: "", fruit2: "", fruit3: "", rolling: false,
    audioDetails: {
        url: null,
        blob: null,
        chunks: null,
        duration: {
          h: 0,
          m: 0,
          s: 0
        }
      }
  }

  slotRef = [createRef(), createRef(), createRef()];
  // to trigger roolling and maintain state
  roll = () => {
    this.setState({
      rolling: true
    });
    setTimeout(() => {
      this.setState({ rolling: false });
    }, 700);

    // looping through all 3 slots to start rolling
    this.slotRef.forEach((slot, i) => {
      // this will trigger rolling effect
      const selected = this.triggerSlotRotation(slot.current);
      this.setState({ [`fruit${i + 1}`]: selected });
    });
    
    var randomTextIndex = this.state.randomTextIndex
    while (randomTextIndex == this.state.randomTextIndex) {
      randomTextIndex = Math.floor(
        Math.random() * this.state.settingList.length
      );
    }    
    this.setState({randomTextIndex})
  }
  // this will create a rolling effect and return random selected option
  triggerSlotRotation = ref => {
    function setTop(top) {
      ref.style.top = `${top}px`;
    }
    let options = ref.children;
    let randomOption = Math.floor(
      Math.random() * this.state.fruits.length
    );
    let choosenOption = options[randomOption];
    setTop(-choosenOption.offsetTop + 2);
    return this.state.fruits[randomOption];
  }

  handleAudioStop(data){
    this.setState({ audioDetails: data });
  }

  handleAudioUpload(file) {
    if (file) {
      const fd = new FormData()
      fd.append("audioFile", file, "test.webm")
      fd.append('id', this.state.id);
      axios.post('/guests/audio', fd)
      .then(res => {
        const { id } = res.data;
        this.setState({id: id})
      })
      .catch(err => console.log(err));
    }
  }

  componentDidMount() {
    axios.get('/settings/all')
      .then(res => {
        this.setState({ settingList: res.data });
        console.log(res.data)
      });
  }

  handleCountDown(data) {
  }

  handleReset() {
    const reset = {
      url: null,
      blob: null,
      chunks: null,
      duration: {
        h: 0,
        m: 0,
        s: 0
      }
    };
    this.setState({ audioDetails: reset });
  }

  handleRegister() {  
    const data = {
      id: this.state.id,
      name: this.state.name,
      country: this.state.country,
      home: this.state.home,
      age: this.state.age,
      voice: this.state.voice,
      pitch: this.state.pitch,
      pace: this.state.pace,
      gender: this.state.gender,
      updatedDate: new Date(),
    }

    if (this.state.id){
      axios.put('/guests/update', data)
      .then(res => {
      })
      .catch(err => console.log(err));
    }else{
      axios.post('/guests/register', data)
        .then(res => {
          const { id } = res.data;
          this.setState({id: id})
        })
        .catch(err => console.log(err));
    }
  }

  handleIndex = event => {
    const { email, password } = event.target;
    const data = {
      email: email.value,
      password: password.value,
    }

    this.props.login(data);

    event.preventDefault();
  }

  handleChange = prop => event => {
    this.setState({ [prop]: event.target.value });
  }

  render() {
    const { classes } = this.props;
    return (
      <div>    
            <Grid
                container
                direction="row"
                align = "center" 
                justify = "center" 
                alignItems = "center"
            >
              <Grid item xs={12}>
                <Typography style={{ fontSize: "2rem" }}>
                    Flip Website
                </Typography>
              </Grid>
            </Grid>
            <Grid
                container
                justify="space-between"
                direction="row"
                alignItems="center"
            >
              <Grid item xs={12}>
                <Typography style={{ marginTop: 20, marginBottom: 20 }}>
                    Thank you for participating in our random voice sample project! 
                </Typography>
              </Grid>
            </Grid>
            <Grid
                container
                justify="space-between"
                direction="row"
                alignItems="center"
            >
              <Grid item>
                <Typography>
                    Optional Survey 
                </Typography>
              </Grid>
              <Grid item>
                <Button variant="flat" onClick={()=>{this.setState({hide: !this.state.hide})}}>
                    {this.state.hide ? 'Show' : 'Hide'}
                </Button>
              </Grid>
            </Grid>
            { !this.state.hide && 
              <Grid
                  container
                  className={classes.fill}
              >
              <Grid
                  container
                  justify="space-between"
                  direction="row"
                  alignItems="center"
              >
                <Grid item xs={12}  sm={4}>
                  <FormControl
                      className={[classes.margin, classes.fill].join(' ')}
                    >
                    <InputLabel htmlFor="name">Full Name</InputLabel>
                    <Input
                      name="name"
                      id="name"
                      value={this.state.name}
                      onChange={this.handleChange('name')}
                    />
                  </FormControl>
                </Grid>
                <Grid item xs={12}  sm={4}>
                  <FormControl
                      className={[classes.margin, classes.fill].join(' ')}
                    >
                    <InputLabel htmlFor="home">Home</InputLabel>
                    <Input
                      name="home"
                      id="home"
                      value={this.state.home}
                      onChange={this.handleChange('home')}
                    />
                  </FormControl>
                </Grid>
                <Grid item xs={12}  sm={4}>
                  <FormControl
                      className={[classes.margin, classes.fill].join(' ')}
                    >
                    <InputLabel htmlFor="country">Country</InputLabel>
                    <Input
                      name="country"
                      id="country"
                      value={this.state.country}
                      onChange={this.handleChange('country')}
                    />
                  </FormControl>
                </Grid>
              </Grid>

              <Grid
                  container
                  justify="space-between"
                  direction="row"
                  alignItems="center"
              >
                <Grid item xs={4}  sm={3}>
                  <Typography>
                      I am : 
                  </Typography>
                </Grid>
                <Grid item xs={8}  sm={9}>
                  <RadioGroup
                    className={classes.fill}
                    defaultValue="male"
                    value={this.state.gender}
                    onChange={this.handleChange('gender')}
                    row
                  >
                    <FormControlLabel value="male" control={<Radio />} label="male" />
                    <FormControlLabel value="female" control={<Radio />} label="female" />
                    <FormControlLabel value="decline" control={<Radio />} label="decline" />
                  </RadioGroup>
                </Grid>
              </Grid>
              <Grid
                  container
                  justify="space-between"
                  direction="row"
                  alignItems="center"
              >
                <Grid item xs={4}  sm={3}>
                  <Typography>
                      My age : 
                  </Typography>
                </Grid>
                <Grid item xs={8}  sm={9}>
                  <RadioGroup
                    className={classes.fill}
                    defaultValue="male"
                    value={this.state.age}
                    onChange={this.handleChange('age')}
                    row
                  >
                    <FormControlLabel value="13-16" control={<Radio />} label="13-16" />
                    <FormControlLabel value="17-24" control={<Radio />} label="17-24" />
                    <FormControlLabel value="25-35" control={<Radio />} label="25-35" />
                    <FormControlLabel value="35+" control={<Radio />} label="35+" />
                  </RadioGroup>
                </Grid>
              </Grid>
              <Grid
                  container
                  justify="space-between"
                  direction="row"
                  alignItems="center"
              >
                <Grid item xs={4}  sm={3}>
                  <Typography>
                      My voice : 
                  </Typography>
                </Grid>
                <Grid item xs={8}  sm={9}>
                  <RadioGroup
                    className={classes.fill}
                    defaultValue="male"
                    value={this.state.voice}
                    onChange={this.handleChange('voice')}
                    row
                  >
                    <FormControlLabel value="soft" control={<Radio />} label="soft" />
                    <FormControlLabel value="medium" control={<Radio />} label="medium" />
                    <FormControlLabel value="strength" control={<Radio />} label="strength" />
                    <FormControlLabel value="strong" control={<Radio />} label="strong" />
                    <FormControlLabel value="decline" control={<Radio />} label="decline" />
                  </RadioGroup>
                </Grid>
              </Grid>
              <Grid
                  container
                  justify="space-between"
                  direction="row"
                  alignItems="center"
              >
                <Grid item xs={4}  sm={3}>
                  <Typography>
                    My voice pitch : 
                  </Typography>
                </Grid>
                <Grid item xs={8}  sm={9}>
                  <RadioGroup
                    className={classes.fill}
                    defaultValue="male"
                    value={this.state.pitch}
                    onChange={this.handleChange('pitch')}
                    row
                  >
                    <FormControlLabel value="high" control={<Radio />} label="high" />
                    <FormControlLabel value="medium" control={<Radio />} label="medium" />
                    <FormControlLabel value="low" control={<Radio />} label="low" />
                    <FormControlLabel value="decline" control={<Radio />} label="decline" />
                  </RadioGroup>
                </Grid>
              </Grid>
              <Grid
                  container
                  justify="space-between"
                  direction="row"
                  alignItems="center"
              >
                <Grid item xs={4}  sm={3}>
                  <Typography>
                      My normal pace : 
                  </Typography>
                </Grid>
                <Grid item xs={8}  sm={9}>
                  <RadioGroup
                    className={classes.fill}
                    defaultValue="pace"
                    value={this.state.pace}
                    onChange={this.handleChange('pace')}
                    row
                  >
                    <FormControlLabel value="slower" control={<Radio />} label="slower" />
                    <FormControlLabel value="average" control={<Radio />} label="average" />
                    <FormControlLabel value="fast" control={<Radio />} label="fast" />
                    <FormControlLabel value="decline" control={<Radio />} label="decline" />
                  </RadioGroup>  
                </Grid>
              </Grid>  
              <Grid
                container
                align = "center" 
                justify = "center" 
                alignItems = "center"
              >
                <Grid item xs={12}>
                  <Typography>
                      Last One: If I could change my voice to sound like any famous person's voice it would definitely be
                  </Typography>
                </Grid>
                <Grid item xs={12}>
                  <Button variant="raised" color="primary" style={{margin:30}} onClick={()=>{
                    this.handleRegister()
                    this.setState({hide: !this.state.hide}
                    )}}>
                    Save All
                  </Button>
                </Grid>
              </Grid>              
              </Grid>
            }
            { this.state.hide &&
              <Grid
                  container
                  align = "center" 
                  justify = "center" 
                  alignItems = "center"
                  direction="row"
              >
                <Grid item xs={12} sm={12} md={6}>
                  <div className="SlotMachine">
                    <div className="slot">
                      <section>
                        <div className="container" ref={this.slotRef[0]}>
                          {this.state.fruits.map((fruit, i) => (
                            <div key={i}>
                              <span>{fruit}</span>
                            </div>
                          ))}
                        </div>
                      </section>
                    </div>
                    <div className="slot">
                      <section>
                        <div className="container" ref={this.slotRef[1]}>
                          {this.state.fruits.map((fruit, i) => (
                            <div key={i}>
                              <span>{fruit}</span>
                            </div>
                          ))}
                        </div>
                      </section>
                    </div>
                    <div className="slot">
                      <section>
                        <div className="container" ref={this.slotRef[2]}>
                          {this.state.fruits.map((fruit, i) => (
                            <div key={i}>
                              <span>{fruit}</span>
                            </div>
                          ))}
                        </div>
                      </section>
                    </div>
                    <div
                      className={!this.state.rolling ? "roll rolling" : "roll"}
                      onClick={!this.state.rolling && this.roll}
                      disabled={this.state.rolling}
                    >
                      {this.state.rolling ? "Rolling..." : "ROLL"}
                    </div>
                  </div>
                  { this.state.settingList.length > this.state.randomTextIndex && (
                    <div style={{fontSize:25}}>
                      {this.state.settingList[this.state.randomTextIndex].text.split('\n').map( (it, i) => <div key={'x'+i}>{it}</div>)}
                    </div>
                  )}
                </Grid>
                <Grid item xs={12} sm={12} md={6}>
                  <Recorder
                      record={true}
                      title={"Flip recording"}
                      audioURL={this.state.audioDetails.url}
                      showUIAudio
                      handleAudioStop={data => this.handleAudioStop(data)}
                      handleAudioUpload={data => this.handleAudioUpload(data)}
                      handleCountDown={data => this.handleCountDown(data)}
                      handleReset={() => this.handleReset()}
                      mimeTypeToUseWhenRecording={`audio/webm`} // For specific mimetype.
                  />
                </Grid>
              </Grid>
            }
            <Grid
                container
                align = "center" 
                justify = "center" 
                alignItems = "center"
                direction="row"
            >
            <Grid item xs={12}  style={{marginTop:30}}>
              <Typography>
                  File Repository to be shared with Flip Team
              </Typography>
            </Grid>
          </Grid>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  user: state.user.data,
  error: state.error,
})

export default withRouter(
  compose(
    withStyles(styles),
    connect(mapStateToProps, { login }),
  )(Index)
);


